import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { scrollTo, getScrollbarWidth } from "./HelperFunctions";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import CodeIcon from "@material-ui/icons/Code";
import Grid from "@material-ui/core/Grid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faItchIo } from "@fortawesome/free-brands-svg-icons";
import { Light as SyntaxHighlighter } from 'react-syntax-highlighter';
import csharp from 'react-syntax-highlighter/dist/esm/languages/hljs/csharp';
import vs2015 from 'react-syntax-highlighter/dist/esm/styles/hljs/vs2015';

SyntaxHighlighter.registerLanguage('csharp', csharp);

const defaultData = {
  engine: "Unity",
  language: "C#",
  sourceControl: "git"
};

const useStyles = makeStyles((theme) => ({
  cardRoot: (props) => ({
    backgroundColor: "#013B66",
    background: "linear-gradient(-45deg, #045f77, #013B66)"
  }),
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  avatar: (props) => ({
    backgroundColor: props.iconBackground
  }),
  dividerMargin: {
    marginTop: "30px",
    marginBottom: "18px"
  },
  button: {
    margin: theme.spacing(1)
  },
  watermark: {
    position: "absolute",
    textTransform: "uppercase",
    top: "8px",
    right: (16 + getScrollbarWidth()) + "px",
    opacity: .2,
    userSelect: "none"
  },
  code: {
    maxHeight: "600px",
    fontFamily: "Consolas, monospace",
    fontWeight: 600
  },
  watermarkParent: {
    position: "relative"
  }
}));

function Project(props) {
  const classes = useStyles(props);
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const id = props.projectName.split("(")[0].replace(/([^A-Za-z0-9])/g, "");

  const engine = (props.details && props.details.engine) || defaultData.engine;
  const language =
    (props.details && props.details.language) || defaultData.language;
  const sourceControl =
    (props.details && props.details.sourceControl) || defaultData.sourceControl;

  return (
    <Card className={classes.cardRoot} id={id}>
      <CardHeader
        avatar={
          <Avatar
            aria-label="Game's icon"
            className={classes.avatar}
            src={props.iconSrc}
          >
            {id.replace(/([^A-Z])/g, "")}
          </Avatar>
        }
        title={props.projectName}
        subheader={props.subheader}
      />
      {props.codeSample ?
        <CardMedia className={classes.watermarkParent}>
          <Typography variant="h3" align="right" className={classes.watermark}>
            Sample code
          </Typography>
          <SyntaxHighlighter
            className={classes.code}
            language="csharp"
            style={vs2015}
            showLineNumbers
            // wrapLongLines
          >
          {props.codeSample}
          </SyntaxHighlighter>
        </CardMedia>
      : ""}
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          {props.description}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        {props.itchUrl ? (
          <Button
            startIcon={<FontAwesomeIcon icon={faItchIo} />}
            href={props.itchUrl}
            target="_blank"
          >
            itch.io
          </Button>
        ) : (
          ""
        )}
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardMedia
          className={classes.media}
          image={props.screenshotSrc}
          title="Screenshot from the game"
        />
        <CardContent>
          {props.details ? (
            <React.Fragment>
              {/* Project */}
              <Typography paragraph variant="h5">
                Project
              </Typography>
              <Typography paragraph>Engine: {engine}</Typography>
              <Typography paragraph>Language: {language}</Typography>
              <Typography paragraph>Source control: {sourceControl}</Typography>
              <Divider className={classes.dividerMargin} />
              {/* Team */}
              <Typography paragraph variant="h5">
                Team
              </Typography>
              {props.details.team.map((t, i) => (
                <Typography paragraph key={i}>
                  {t}
                </Typography>
              ))}
              {props.details.myResponsibilities ? (
                <React.Fragment>
                  <Divider className={classes.dividerMargin} />
                  {/* What I did */}
                  <Typography paragraph variant="h5">
                    What I did
                  </Typography>
                  <Typography paragraph>I was responsible for:</Typography>
                  <ul>
                    {props.details.myResponsibilities.map((r, i) => (
                      <li key={i}>
                        <Typography paragraph>{r}</Typography>
                      </li>
                    ))}
                  </ul>
                </React.Fragment>
              ) : (
                ""
              )}
              {props.details.techniquesEngine &&
              props.details.techniquesLanguage ? (
                <React.Fragment>
                  <Divider className={classes.dividerMargin} />
                  {/* Techniques used */}
                  <Typography paragraph variant="h5">
                    Techniques I used
                  </Typography>
                  <Typography paragraph>{engine}:</Typography>
                  <ul>
                    {props.details.techniquesEngine.map((t, i) => (
                      <li key={i}>
                        <Typography paragraph>{t}</Typography>
                      </li>
                    ))}
                  </ul>
                  <Typography paragraph>{language}:</Typography>
                  <ul>
                    {props.details.techniquesLanguage.map((t, i) => (
                      <li key={i}>
                        <Typography paragraph>{t}</Typography>
                      </li>
                    ))}
                  </ul>
                </React.Fragment>
              ) : (
                ""
              )}
              <br />
              <Grid container spacing={3}>
                <Grid item xs={9}>
                  {props.details.sampleClassUrl ? (
                    <Button
                      variant="contained"
                      color="default"
                      className={classes.button}
                      startIcon={<CodeIcon />}
                      href={props.details.sampleClassUrl}
                      target="_blank"
                    >
                      Sample class
                    </Button>
                  ) : (
                    ""
                  )}
                </Grid>
                <Grid item xs={3} container justify="flex-end">
                  <IconButton
                    className={clsx(classes.expand, {
                      [classes.expandOpen]: expanded
                    })}
                    onClick={() => {
                      handleExpandClick();
                      scrollTo(id);
                    }}
                    aria-expanded={expanded}
                    aria-label="show more"
                  >
                    <ExpandMoreIcon />
                  </IconButton>
                </Grid>
              </Grid>
            </React.Fragment>
          ) : (
            ""
          )}
        </CardContent>
      </Collapse>
    </Card>
  );
}

export default Project;
