import React from "react";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import MainContent from "./MainContent";
import DrawerContent from "./DrawerContent";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex"
  },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0
    }
  },
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth
    },
    opacity: 0.9,
    backdropFilter: "blur(6px)",
    backgroundColor: "#013B66"
    //background:
    //  "linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(9,9,121,1) 100%)" // top bar
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none"
    }
  },
  // necessary for content to be below app bar
  toolbarBelowAppBar: theme.mixins.toolbar,
  toolbarText: {
    display: "flex",
    flexDirection: "column",
    alignSelf: "flex-end"
  },
  toolbar: {
    minHeight: 87,
    alignItems: "flex-start",
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(2)
  },
  drawerPaper: {
    width: drawerWidth
    //backgroundColor: "#000000" // side bar
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3)
    //backgroundColor: "#000000" // main page
    // background: "linear-gradient(-45deg, #056B7D, #056B7D, #013B66, #056B7D)",
    // backgroundSize: "400% 400%",
    // animation: "$gradient 15s ease infinite"
  }
  // "@keyframes gradient": {
  //   "0%": {
  //     backgroundPosition: "0% 50%"
  //   },
  //   "50%": {
  //     backgroundPosition: "100% 50%"
  //   },
  //   "100%": {
  //     backgroundPosition: "0% 50%"
  //   }
  // }
}));

function ResponsiveDrawer(props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const closeDrawer = () => {
    if (!mobileOpen) return;
    setMobileOpen(false);
  };

  const drawer = (
    <div>
      <div className={classes.toolbarBelowAppBar} />
      <DrawerContent closeDrawer={closeDrawer} />
    </div>
  );

  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <div className={classes.toolbarText}>
            <Typography variant="h5" noWrap>
              Arkadiusz Bergmann
            </Typography>
            <Typography noWrap>Junior Unity (C#) Developer</Typography>
          </div>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper
            }}
            ModalProps={{
              keepMounted: true // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main id="main" className={classes.content}>
        <div className={classes.toolbar} />
        <MainContent />
      </main>
    </div>
  );
}

export default ResponsiveDrawer;
