import React from "react";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import EmailIcon from "@material-ui/icons/Email";

function Contact(props) {
  const getHeight = () => props.windowInnerHeight - 125 + "px";

  return (
    <div style={{ minHeight: getHeight() }}>
      <Typography variant="h4" id="Contact" paragraph>
        Contact
      </Typography>

      <Button
        href="mailto:arkadiuszbergmann@gmail.com"
        startIcon={<EmailIcon />}
        style={{ userSelect: "text" }}
        variant="outlined"
      >
        arkadiuszbergmann@gmail.com
      </Button>
    </div>
  );
}

export default Contact;
